.box {
  position: relative;
  background: #ffffff;
  //border-top: 1px solid #c1c1c1;
  border: none;
  margin-bottom: 20px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  width: 100%;
  //border: 1px solid #dcdcdc;
  border-top: none;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);
  //box-shadow: 0 1px 6px 0 rgba(0,0,0,.12),0 1px 6px 0 rgba(0,0,0,.12);
  
}

.box.box-primary {
  border-top-color: @gray-light;
}
.box.box-info {
  border-top-color: #00c0ef;
}
.box.box-danger {
  border-top-color: #f56954;
}
.box.box-warning {
  border-top-color: #f39c12;
}
.box.box-success {
  border-top-color: #00a65a;
}
.box.height-control .box-body {
  max-height: 300px;
  overflow: auto;
}
.box .box-header {
  position: relative;
  -webkit-border-top-left-radius: 3px;
  -webkit-border-top-right-radius: 3px;
  -webkit-border-bottom-right-radius: 0;
  -webkit-border-bottom-left-radius: 0;
  -moz-border-radius-topleft: 3px;
  -moz-border-radius-topright: 3px;
  -moz-border-radius-bottomright: 0;
  -moz-border-radius-bottomleft: 0;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  //border-bottom: 0px solid #f4f4f4;
  border-bottom: 1px solid @gray-light;
  color: #444;
}
.box .box-header:before,
.box .box-header:after {
  display: table;
  content: " ";
}
.box .box-header:after {
  clear: both;
}
.box .box-header > .fa,
.box .box-header > .glyphicon,
.box .box-header > .ion,
.box .box-header .box-title {
/*  display: inline-block;
  padding: 10px 10px 10px 10px;
  margin: 0;
  font-size: 20px;
  font-weight: 400;
  float: left;
  cursor: default;*/
  font-weight: bold;
  float: left;
  .font-size(@font-size-h4);
  font-family: @font-family-serif;
  margin-left: 10px;
  color: @brand-primary;
  margin-top: 8.5px;
  margin-bottom: 8.5px;
}


.box .box-header a {
  color: #444;
}
.box .box-header > .box-tools {
  padding: 9px 15px 9px 10px;
  border-left: 1px solid #ddd;
  margin-bottom: 0px;
}
.box .box-header > .box-tools i {
  color: @brand-primary;
}

.box .box-header > .box-tools a {
  cursor: pointer;
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none;          /* Likely future */  
}

/* Flèche collapsed box */
.box .box-header.box-expendable  > .box-tools:after {
  font-family: 'Glyphicons Halflings';
  content:"\E602";
  color: @brand-primary; 
}

.box .box-header.box-expendable.collapsed > .box-tools:after {
  font-family: 'Glyphicons Halflings';
  content:"\E601"; 
  color: @brand-primary;
}

.box .box-action {
  margin-bottom: 10px;
  padding: 10px;  
  margin-top: -1px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

.box .box-action form {
  margin-bottom: 0px;
}

.action-zone button, .action-zone a {
  margin-right: 15px;
}


/* SECTION */
.box.section .box-header {
  cursor: pointer;
}

.box .box-expendable {
  cursor: pointer;  
}

.box.section {
  border-radius: 0px;
  margin-top: 17px;
}


.panel-default > .panel-heading {
  color: @brand-primary;
  font-weight: bold;
}

.box.section .box-header .box-title {
  color: #333333;
    font-weight: bold;
}


.section-action {
  margin-top: -10px;
}

.section-action:before {
  content :"\E223";
  font-family: 'Glyphicons Halflings';
  color: #2280bf;
  .font-size(22);
  margin-right: 6px;
  position: relative;
  top: 7px;
}


/* Gestion du chevron de section */
.box.section .box-header.collapsed .box-title:after {
  font-family: 'Glyphicons Halflings';
  content:"\E602"; /*"\E602"*/
  background-color: #edeef0;
  border-radius: 50%;
  padding-left: 3.5px;
  padding-right: 3px;
  border: 1px solid #ddd;
  margin-left: 10px;
  position: relative;
  top:1px;
  color: @brand-primary; /* bleu theme */

}

.box.section .box-header .box-title:after {
  font-family: 'Glyphicons Halflings';
  content:"\E601"; /*"\E602"*/
  background-color: #edeef0;
  border-radius: 50%;
  padding-left: 3.5px;
  padding-right: 3px;
  border: 1px solid #ddd;
  margin-left: 10px;
  position: relative;
  top:1px;
  color: @brand-primary; /* bleu theme */

}



.box .box-body {
  padding: 10px;
  -webkit-border-top-left-radius: 0;
  -webkit-border-top-right-radius: 0;
  -webkit-border-bottom-right-radius: 3px;
  -webkit-border-bottom-left-radius: 3px;
  -moz-border-radius-topleft: 0;
  -moz-border-radius-topright: 0;
  -moz-border-radius-bottomright: 3px;
  -moz-border-radius-bottomleft: 3px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}
.box .box-body > table,
.box .box-body > .table {
  margin-bottom: 0;
}
.box .box-body.chart-responsive {
  width: 100%;
  overflow: hidden;
}
.box .box-body > .chart {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.box .box-body > .chart svg,
.box .box-body > .chart canvas {
  width: 100%!important;
}
.box .box-body .fc {
  margin-top: 5px;
}
.box .box-body .fc-header-title h2 {
  .font-size(@font-size-h2);
  line-height: 1.6em;
  color: #666;
  margin-left: 10px;
}
.box .box-body .fc-header-right {
  padding-right: 10px;
}
.box .box-body .fc-header-left {
  padding-left: 10px;
}
.box .box-body .fc-widget-header {
  background: #fafafa;
  box-shadow: inset 0px -3px 1px rgba(0, 0, 0, 0.02);
}
.box .box-body .fc-grid {
  width: 100%;
  border: 0;
}
.box .box-body .fc-widget-header:first-of-type,
.box .box-body .fc-widget-content:first-of-type {
  border-left: 0;
  border-right: 0;
}
.box .box-body .fc-widget-header:last-of-type,
.box .box-body .fc-widget-content:last-of-type {
  border-right: 0;
}
.box .box-body .table {
  margin-bottom: 0;
}
.box .box-body .full-width-chart {
  margin: -19px;
}
.box .box-body.no-padding .full-width-chart {
  margin: -9px;
}

.box .box-footer {
  border-top: 1px solid #dddddd;
  -webkit-border-top-left-radius: 0;
  -webkit-border-top-right-radius: 0;
  -webkit-border-bottom-right-radius: 3px;
  -webkit-border-bottom-left-radius: 3px;
  -moz-border-radius-topleft: 0;
  -moz-border-radius-topright: 0;
  -moz-border-radius-bottomright: 3px;
  -moz-border-radius-bottomleft: 3px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 10px;
  //background-color: #ffffff;
}
.box-footer.row {
  margin-left: 0px;
  margin-right: 0px;
}


.box.box-solid {
  border-top: 0px;
}
.box.box-solid > .box-header {
  padding-bottom: 0px!important;
}
.box.box-solid > .box-header .btn.btn-default {
  background: transparent;
}
.box.box-solid.box-primary > .box-header {
  color: #fff;
  background: #3c8dbc;
  background-color: #3c8dbc;
}
.box.box-solid.box-primary > .box-header a {
  color: #444;
}
.box.box-solid.box-info > .box-header {
  color: #fff;
  background: #00c0ef;
  background-color: #00c0ef;
}
.box.box-solid.box-info > .box-header a {
  color: #444;
}
.box.box-solid.box-danger > .box-header {
  color: #fff;
  background: #f56954;
  background-color: #f56954;
}
.box.box-solid.box-danger > .box-header a {
  color: #444;
}
.box.box-solid.box-warning > .box-header {
  color: #fff;
  background: #f39c12;
  background-color: #f39c12;
}
.box.box-solid.box-warning > .box-header a {
  color: #444;
}
.box.box-solid.box-success > .box-header {
  color: #fff;
  background: #00a65a;
  background-color: #00a65a;
}
.box.box-solid.box-success > .box-header a {
  color: #444;
}
.box.box-solid > .box-header > .box-tools .btn {
  border: 0;
  box-shadow: none;
}
.box.box-solid.collapsed-box .box-header {
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.box.box-solid[class*='bg'] > .box-header {
  color: #fff;
}
.box .box-group > .box {
  margin-bottom: 5px;
}
.box .knob-label {
  text-align: center;
  color: #333;
  font-weight: 100;
  .font-size(@font-size-base);
  margin-bottom: 0.3em;
}

.page-footer {
  margin-bottom: 30px;
}



.shadow-z-1 {
  box-shadow: 0 1px 6px 0 rgba(0,0,0,.12),0 1px 6px 0 rgba(0,0,0,.12);
}
.shadow-z-2 {
  box-shadow: 0 8px 17px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);
}
.shadow-z-3 {
box-shadow: 0 12px 15px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);
}
