.pace{
  > .pace-progress{
    background-color: @bl-light;
  }
}


.minimalize-styl-2 {  
  /* Pour annuler l'état du bouton du menu visited ou focus */
  &:focus {
    background-color: @bl-light!important;  
  }
}


// style de l'élément actif du menu
.menu.menu-y{
  > div > ul,
  > ul {
    > li  {

	    &.active{
         border-left: 4px solid @bl-light;
         &:before{
          color: @bl-light;
        }
        > a{
          //color: @bl-light;
  			} 
	    }

	    &.stretchable{
	        > ul.nav-second-level {
	          > li{
	            &.active {
                border-left: 4px solid @bl-light;
                //background-color: @bl-light;
                > a {
	                color: @bl-light;
                  &:hover, 
                  &:focus {
                    //background-color: @bl-light;
                  }
	              }
              }
	          }
	        }
	    }
       .glyphicons,
        .fa{
          color: @bl-light;
        }


	}

  }
}


.nav-tabs {
  border-bottom: 4px solid @bl-light;
  > li.active > a{
    background-color: @bl-light;
  }
}
.navbar.navbar-inverse {
  background-color: @bl-light !important;
}

.btn{
  &.btn-primary{
    //.linear-gradient(lighten(@bl-light, 5%), @bl-light);
    background-color: @bl-light;
    border: 1px solid darken(@bl-light, 10%);
    &:hover,&:focus{
      background: none;
      //background-color: @bl-light;
      background-color: darken(@bl-light, 10%);
    }
  }
}

.box .box-header .box-title {
  color: @bl-light;
}
.box .box-header > .box-tools i, .box .box-header > .box-tools:after {
  color: @bl-light;
}



table th a.unsorted:after {
  color: @bl-light!important;
}
table th a.sortasc:after {
  color: @bl-light!important;
}
table th a.sortdesc:after {
  color: @bl-light!important;
} 
ul.pagination li a {
  color: @bl-light!important;
}

.box .glyphicons {
  color: @bl-light;
}

.btn {
  .glyphicons {
  color: @white;
  }
  &.btn-default {
    .glyphicons {
      color: @bl-light;
    }
  }
}

.section-action:before {
  color: @bl-light;
}

.box.section .box-header .box-title:after {
 color: @bl-light;  
}


.modal-content{
  .modal-header{
    background-color: @bl-light;
  }
}

.navbar-fixed-top:after{
  width: 100%;
  height: 4px;

  filter:progid:DXImageTransform.Microsoft.gradient(GradientType=1,startColorstr=#FEC465, endColorstr=#F49AC1); 
  background-image:-moz-linear-gradient(left, #FEC465 0%, #F49AC1 50%,#9D4252 100%);
  background-image:linear-gradient(left, #FEC465 0%, #F49AC1 50%,#9D4252 100%);
  background-image:-webkit-linear-gradient(left, #FEC465 0%, #F49AC1 50%,#9D4252 100%);
  background-image:-o-linear-gradient(left, #FEC465 0%, #F49AC1 50%,#9D4252 100%);
  background-image:-ms-linear-gradient(left, #FEC465 0%, #F49AC1 50%,#9D4252 100%);
  background-image:-webkit-gradient(linear, left bottom, right bottom, color-stop(0%,#FEC465), color-stop(50%,#F49AC1),color-stop(100%,#9D4252));
}


[type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    color: @bl-light;
  }

input[type="radio"]+label:after {
  background-color: @bl-light;
}