

/* btn */
.input-group-btn button {
  border-radius: 3px;
  background-color: #F7F7F7!important;
  box-shadow:none!important;
}

.input-group-btn button span {
  color: #2280BF; /* bleu theme */
}

.btn{

  font-family: @font-family-serif;
  font-weight: bold;

  border-top-left-radius: 0px;
  border-top-right-radius: 7px;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 0px;

  transition: all 0.2s ease-out;

  //.linear-gradient(@white, #e0e0e0);

  //box-shadow: inset 0 1px 0 rgba(255, 255, 255, .15),
  //                  0 1px 1px rgba(0, 0, 0, .075);
  box-shadow: 0 1px 6px 0 rgba(0,0,0,.12),0 1px 6px 0 rgba(0,0,0,.12);

  padding: 3px 15px 3px 15px;
  //border: 1px solid #bbbbbb;
  border:none;

  //padding: 4px 15px 4px 15px;
  

  background-color: @white;

  overflow: hidden;
  position: relative;

  &:hover,&:focus,&:active{
    background: none;
    background-color: #e0e0e0;
    outline: 0!important;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }


  &.btn-default{
    border: 1px solid #bbbbbb;

    //.linear-gradient(lighten(@brand-default  , 5%), @brand-default );
    //background-color: @brand-default;
    //border: 1px solid darken(@brand-default, 10%);
    //color: @white;
/*    &:hover,&:focus{
      background: none;
      background-color: lighten(@brand-default  , 10%);
    }
*/
  }
  
  &.btn-success{
    padding: 4px 15px 4px 15px;
    //.linear-gradient(lighten(@brand-success, 5%), @brand-success);
    background-color: @brand-success;
    //border: 1px solid darken(@brand-success, 10%);
    border:none!important;
    color: @white;
    &:hover,&:focus{
      background: none;
      background-color: darken(@brand-success , 10%);
    }
  }

  &.btn-primary{
    padding: 4px 15px 4px 15px;
    //.linear-gradient(lighten(@brand-default, 5%), @brand-default);
    background-color: @brand-default;
    //border: 1px solid darken(@brand-default, 10%);
    border:none!important;
    &:hover,&:focus{
      background: none;
      background-color:  darken(@brand-default, 10%);
    }
  }

  &.btn-info{
    padding: 4px 15px 4px 15px;
    //.linear-gradient(lighten(@brand-primary, 5%), @brand-primary);
    background-color: @brand-primary;
    //border: 1px solid darken(@brand-primary, 10%);
    border:none!important;
    &:hover,&:focus{
      background: none;
      background-color:  darken(@brand-primary, 10%);
    }
  }

  &.btn-warning{
    padding: 4px 15px 4px 15px;
    //.linear-gradient(lighten(@brand-warning, 5%), @brand-warning);
    background-color: @brand-warning;
    //border: 1px solid darken(@brand-warning, 10%);
    border:none!important;
    &:hover,&:focus{
      background: none;
      background-color:  darken(@brand-warning, 10%)
    }
  }

  &.btn-danger{
    padding: 4px 15px 4px 15px;
    //.linear-gradient(lighten(@brand-danger, 5%), @brand-danger);
    background-color: @brand-danger;
    //border: 1px solid darken(@brand-danger, 10%);
    border:none!important;
    &:hover,&:focus{
      background: none;
      background-color:  darken(@brand-danger, 10%);
    }
  }

  &.btn-lg{
    padding: 7px 15px 7px 15px;
  }
  &.btn-sm{
    padding: 1px 15px 1px 15px;
  }
  &.btn-xs{
    padding: 0px 15px 0px 15px;
  }

}


.btn:hover {
  box-shadow: 0 3px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);
  transition: all 0.2s ease-out;
}




.circle {
  display: block;
  position: absolute;
  background: rgba(0, 0, 0, 0.075);
  border-radius: 50%;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}
.circle.animate {
  -webkit-animation: effect 0.65s linear;
          animation: effect 0.65s linear;
}

@-webkit-keyframes effect {
  100% {
    opacity: 0;
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
}

@keyframes effect {
  100% {
    opacity: 0;
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
  }
}

