body{
  
  //overflow-y: hidden;

  background-color:@gray-lighter;
  //background-color:@white;


  input[type=text],
  input[type=number],
  input[type=email],
  input[type=password],
  input[type=search],
  input[type=tel],
  input[type=url],
  textarea,
  select{
    &:hover{
      border: 1px solid @primary-2;
    }
  }

  // Chosen
  .chosen-container{

    //height: @input-height-base !important;
    .chosen-single,
    .chosen-choices{
      //height: @input-height-base !important;
      border-radius: @input-border-radius;
      background: none;
      .search-choice{
        margin: 3px 2px 2px 0 !important;
      }
    }
    .chosen-drop{
      .chosen-results > li.highlighted{
        background: none;
        background-color: @dropdown-link-hover-bg;
        color: #000000;
      }
    }
    &.chosen-disabled{
      opacity: 1 !important;
      background-color: @input-bg-disabled;
    }
  }

  .has-error .chosen-container .chosen-choices {
    border:1px solid @brand-danger;
  }
  // End of Chosen

}

// Chosen responsive fix
.chosen-container-active .chosen-choices{
  border: 1px solid @input-border-focus;
}

[class*="col-"] .chosen-container {
    width:100%!important;
}
[class*="col-"] .chosen-container .chosen-search input[type="text"] {
    padding:2px 4%!important;
    width:90%!important;
    margin:5px 2%;
}
[class*="col-"] .chosen-container .chosen-drop {
    width: 100%!important;
}
// End of chosen responsive fix


// Feedback states
.has-success{
  .form-control-validation(@form-label-color; @brand-success; @input-bg);
  .form-control-feedback {
    color: @brand-success;
  }
}
.has-warning {
  .form-control-validation(@form-label-color; @brand-warning; @input-bg);
  .form-control-feedback {
    color: @brand-warning;
  }
}
.has-error {
  .form-control-validation(@form-label-color; @brand-danger; @input-bg);
  .form-control-feedback {
    color: @brand-danger;
  }
}

.input-group-addon{
  padding: 2px 12px 3px 12px;
  //background-color: @gray-light;
}

.input-group {
  &.input-group-lg {
    .input-group-btn > .btn {
      padding: 0px 16px;
    }
    .input-group-addon {
      padding: 0px 16px; 
    }
  }
  &.input-group-sm {
    .input-group-addon {
      padding: 0px 10px; 
    }
  }
}



a, div {
  .font-size(@font-size-base);
}

label {
