/*
  Style des checkboxs
*/

/* Base for label styling */
  [type="checkbox"]:not(:checked),
  [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
  }

  [type="checkbox"]:not(:checked) + label,
  [type="checkbox"]:checked + label {
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    padding-top: 3px;
  }

  /* checkbox aspect */
  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before {
    content: '';
    position: absolute;
    left:0; top: 2px;
    width: 19px; height: 19px;
    border: 1px solid #aaa;
    background: #f8f8f8;
    border-radius: 0px;
    //box-shadow: inset 0 1px 3px rgba(0,0,0,.3)
  }
  /* checked mark aspect */
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    content: '✔';
    position: absolute;
    top: 0; left: 4px;
    .font-size(14);
    color: @brand-default;
    line-height: 1.75;
    //-webkit-transition: all .2s;
    //-moz-transition: all .2s;
    //-ms-transition: all .2s;
    //transition: all .2s;
  }
  /* checked mark aspect changes */
  [type="checkbox"]:not(:checked) + label:after {
    opacity: 0;
    //-webkit-transform: scale(0);
    //-moz-transform: scale(0);
    //-ms-transform: scale(0);
    //transform: scale(0);
  }
  [type="checkbox"]:checked + label:after {
    opacity: 1;
    //-webkit-transform: scale(1);
    //-moz-transform: scale(1);
    //-ms-transform: scale(1);
    //transform: scale(1);
  }
  /* disabled checkbox */
  [type="checkbox"]:disabled:not(:checked) + label:before,
  [type="checkbox"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #bbb;
    background-color: #ddd;
  }
  [type="checkbox"]:disabled:checked + label:after {
    color: #999;
  }
  [type="checkbox"]:disabled + label {
    color: #aaa;
  }
  /* accessibility */
  [type="checkbox"]:checked:focus + label:before,
  [type="checkbox"]:not(:checked):focus + label:before,
  [type="checkbox"]:checked:hover + label:before, 
  [type="checkbox"]:not(:checked):hover + label:before {
    //border: 1px dotted blue;
    border: 1px solid #2a7fb9;
    outline: 0;
    webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
  }


/*
  Style des radios
*/

/* masquer les inputs */
input[type="radio"]:checked,
input[type="radio"]:not(:checked) {
  width: 1px;
  height: 1px;
  margin: 0;
  padding: 0;
  opacity: 0;
  position: absolute;
  clip: rect(0 0 0 0);
  clip: rect(0,0,0,0);
  -webkit-appearance: none;
  -moz-appearance:  none;
  -o-appearance:    none;
  appearance:   none;
}

/* les labels */
input[type="radio"]+label {
  float: left;
  padding-left: 26px;
  padding-left: 2.6rem;
  margin-right: 16px;
  margin-right: 1.6rem;
  position: relative;
  cursor: pointer;
}

/* les fonds des inputs */
input[type="radio"]+label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -9px;
  margin-top: -.9rem;
  width: 16px;
  width: 1.6rem;
  height: 16px;
  height: 1.6rem;
  background-color: rgba(255,255,255,.7);
  
  //background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(rgba(255,255,255,.4)), to(rgba(255,255,255,.9)));
  //background: -webkit-linear-gradient(top, rgba(255,255,255,.4), rgba(255,255,255,.9));
  //background:    -moz-linear-gradient(top, rgba(255,255,255,.4), rgba(255,255,255,.9));
  //background:      -o-linear-gradient(top, rgba(255,255,255,.4), rgba(255,255,255,.9));
  //background:   linear-gradient(top, rgba(255,255,255,.4), rgba(255,255,255,.9));

  -webkit-box-shadow: inset .1rem .1rem .2rem rgba(0,0,0,.3), inset 0 0 0 .1rem rgba(0,0,0,.1);
  -moz-box-shadow:  inset .1rem .1rem .2rem rgba(0,0,0,.3), inset 0 0 0 .1rem rgba(0,0,0,.1);
  -o-box-shadow:    inset 1px 1px 2px rgba(0,0,0,.3), inset 0 0 0 1px rgba(0,0,0,.1);
  -o-box-shadow:    inset .1rem .1rem .2rem rgba(0,0,0,.3), inset 0 0 0 .1rem rgba(0,0,0,.1);
  box-shadow:   inset 1px 1px 2px rgba(0,0,0,.2), inset 0 0 0 1px rgba(0,0,0,.1);
  box-shadow:   inset .1rem .1rem .2rem rgba(0,0,0,.3), inset 0 0 0 .1rem rgba(0,0,0,.1);
  -webkit-border-radius:  .3rem;
  -moz-border-radius: .3rem;
  -o-border-radius: 3px;
  -o-border-radius: .3rem;
  border-radius:    3px;
  border-radius:    .3rem;
}
input[type="radio"]+label:before {
  -webkit-border-radius:  .8rem;
  -moz-border-radius: .8rem;
  -o-border-radius: 8px;
  -o-border-radius: .8rem;
  border-radius:    8px;
  border-radius:    .8rem;
}

/* le focus */
input[type='radio']:focus + label:before {
  border: 1px solid #2a7fb9;
  outline: 0;
  -moz-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
  -o-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(84, 180, 226, 0.6);
}


/* les radios */
input[type="radio"]+label:after {
  content: '';
  position: absolute;
  left: 2px;
  left: .2rem;
  top: 50%;
  margin-top: -7px;
  margin-top: -.7rem;
  width: 12px;
  width: 1.2rem;
  height: 12px;
  height: 1.2rem;
  background-color: @brand-default;
  -webkit-border-radius:  .6rem;  
  -webkit-border-radius:  50%;
  -moz-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius:    50%;
  -webkit-box-shadow: inset 0 .1rem .2rem rgba(255,255,255,.6), inset 0 -.1rem .1rem 0 rgba(0,0,0,.5), 0 0 .3rem #000;
  -moz-box-shadow:  inset 0 .1rem .2rem rgba(255,255,255,.6), inset 0 -.1rem .1rem 0 rgba(0,0,0,.5), 0 0 .3rem #000;
  -o-box-shadow:    inset 0 1px 2px rgba(255,255,255,.6), inset 0 -1px 1px 0 rgba(0,0,0,.5), 0 0 3px #000;
  -o-box-shadow:    inset 0 .1rem .2rem rgba(255,255,255,.6), inset 0 -.1rem .1rem 0 rgba(0,0,0,.5), 0 0 .3rem #000;
  box-shadow:   inset 0 1px 2px rgba(255,255,255,.6), inset 0 -1px 1px 0 rgba(0,0,0,.5), 0 0 3px #000;
  box-shadow:   inset 0 .1rem .2rem rgba(255,255,255,.6), inset 0 -.1rem .1rem 0 rgba(0,0,0,.5), 0 0 .3rem #000;
  //-webkit-transform:  scale(0);
  //-moz-transform:   scale(0);
  //-o-transform:   scale(0);
  //transform:    scale(0);
  display: none;
}

input[type="radio"]:checked+label:after {
   display: block;
  //-webkit-transform:  scale(1);
  //-moz-transform:   scale(1);
  //-o-transform:   scale(1);
  //transform:    scale(1);
}
