.navbar{
  &.secondary {
    margin-bottom: 0px;
    z-index: 102;

    &.navbar-inverse{
      background: none!important;
      background-color: @bl-light !important;
      border-style: none!important;
      border-radius: 0px;
    }

  }
  min-height: 42px;
  .navbar-right{
    margin-right: 10px;
  }


  .toolbar ul.nav > li {
    float:left;
  }
  .navbar-right {
    float: right!important;
  }

  .navbar-btn {
    &.btn{
      .glyphicons {
        color: @black;
      }
    }
  }

  .navbar-nav{
    padding-top: 5px;
  }
  .navbar-form{
    padding-top: 6px;
  }
  &.navbar-inverse{
    background: url("../img/connexion_trame.png") no-repeat top left fixed !important;
    background-color: @bl-light !important;
  }
  .navbar-brand {
    padding: 13px 8px;
    font-family: "Oswald";
  }
  .logo-bl {
    height: 20px;
    float:left;
    margin-left: 15px;
    margin-top: 12px;
  }
  .application-title {
    float:left;
    padding-bottom: 6px;
  }
  .main-info {
    float:left;
    padding-top: 11px;
    font-family: @font-family-serif;
    display: none;
  }
  .page-title {
    .font-size(@font-size-large);
    padding-left: 30px;
    color: @white;
  }




  .minimalize-styl-2 {
    border: 1px solid #000!important;
    box-shadow: 0 1px 6px 0 rgba(0,0,0,.12),0 3px 6px 2px rgba(0,0,0,.12)!important;  
    padding: 4px 8px;
    margin: 8px 5px 5px 3px;
    .font-size(11);
    float: left;
    &>.glyphicons {
      margin-top: 1px;
    }
    
    /* Pour annuler l'état du bouton du menu visited ou focus */
    &:focus {
      background-color: @brand-primary!important;  
    }
  }


  .menu-action {
    float:left;
    //width:71px;
    //border-right: 1px solid #000;
    border-right: 1px solid rgba(0,0,0,0.3); 
  }
  
  .collapse {
    display: block;
    visibility: visible;
  }

}


@media screen and (max-width: 768px) {
  .navbar{
    .navbar-nav {
      margin: 0px 0px;
      padding-top: 4px;
    }
    .collapse {
      display: none!important;
      visibility: hidden;
    }
  }
}



/* Couleur navbar quand on click sur un dropdown */
.navbar-inverse .navbar-nav > .open > a, .navbar-inverse .navbar-nav > .open > a:hover, .navbar-inverse .navbar-nav > .open > a:focus {
  background-color: @brand-primary;
  color: #dcdcdc;
}

.navbar-fixed-top {
  border-bottom-width: 0px;
}
