
.menu.menu-y{

  transition: all .5s ease;
  -moz-transition: all .5s ease;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;

  font-family: @font-family-serif;

  //box-shadow: 0px -15px 20px 1px rgba(0, 0, 0, 0.5);
  //box-shadow: 0px -15px 1px 0px rgba(0, 0, 0, 0.25);

  //height:100%;
  //position: fixed;

  background-color: @menu-bg-color;
  //overflow-y: auto;

  > div > ul,
  > ul {
    
    > li {
      
      transition: all .3s ease;
      -moz-transition: all .3s ease;
      -webkit-transition: all .3s ease;
      -o-transition: all .3s ease;


      &.active{
        border-left: 4px solid @brand-primary;
        background: @menu-bg-color-active;
        font-weight: 200;


        > a{
          color: @menu-color-active;
          padding-left: 30px;

          &:focus,
          &:hover{
            //padding-left: 25px;
            background: @menu-bg-hover-color;
            background: transparent;
            color: #000;
          }

        }
      }
      &.stretchable{
        border-left: none!important;
        > ul.nav-second-level {
          > li{
            transition: all .3s ease;
            -moz-transition: all .3s ease;
            -webkit-transition: all .3s ease;
            -o-transition: all .3s ease;

            &.active {

              background: @menu-bg-hover-color;
              //background-color: #EFF1F4;
              //background-color: @brand-primary; /* bleu theme */
              border-left: 4px solid @brand-primary;

              a {
                font-weight: bold!important;  
                //color: white!important;
                
                &:hover, 
                &:focus {
                  //padding-left: 43px;
                  background: @menu-bg-hover-color;
                  //background-color: @brand-primary; /* bleu theme */
                }

              }
            }


            a{
              padding-left: 60px;
              //transition: padding-left 0.3s, color 0.5s;
              font-weight: normal;  

              &:hover{
                //padding-left: 43px;
                //background: @menu-bg-hover-color;
                background-color: #dde1e8; /* gris light */
                color: #000;
              }

            }
             
          }
        }
        //> a{

          //&:after{
            //font-family: 'Glyphicons Halflings';
            //content: "\E224";
            //float: right;
          //}
        //}
        &.active{

          &:before{
            font-family: 'Glyphicons Halflings';
            content: "\E602";
            float: right;
            padding-top: 15px;
            padding-right: 5px;
          }


          //> a {
            //&:after{
            //  .rotate(90deg);
            //}
          //}
        }
      }
      a{
        
        position: relative;
        overflow: hidden;
        cursor: pointer;

        .font-size(@menu-font-size);
        padding: 14px 20px 14px 28px;
        color: @menu-color-active;
        font-weight: bold;
        //transition: padding-left 0.3s, color 0.5s;

        transition: all .3s ease;
        -moz-transition: all .3s ease;
        -webkit-transition: all .3s ease;
        -o-transition: all .3s ease;


        &.secondary-menu-btn {
          text-align: center;
        }


        &:hover{
          //padding-left: 32px;
          background: @menu-bg-hover-color;
          color: #000;
        }


        .glyphicons,
        .fa{
          color: @bl-dark;
        }
        .nav-label{
          padding-left: 10px;
        }
      }
    }
  }
}



#page-wrapper {
  .secondary-menu-btn {
    float:left;
    margin:20px 20px 0px 0px;
    display: none;
  }
}


body.mini-navbar{

  .menu.menu-y {
      //overflow-y: auto;
     ul > li a {
      
      &.secondary-menu-btn {
        text-align: left;
        white-space: nowrap;
        padding-left: 15px;
      }

    }
  }

  .nav{
    li{
      &:hover,
      &:focus{
        > .nav-second-level{
          display: block;
          min-width: 180px;
          height: auto;
        }
      }
      &.active{

        &:before{
            content: none!important;
        }

        .nav-second-level{
          //border-left: 4px solid @menu-border-color-active;
          background: white;
          position: absolute;
          left: 65px;
          top: 0px;
          box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.5);
        }


        &.stretchable{
            > ul.nav-second-level {
              > li {
                a {
                  padding-left: 40px;
                }
              }
            }
        }

      }
      //a:after{
      //  display: none;
      //}

      .nav-second-level{
        display: none;
        background: @menu-bg-color-active;
        position: absolute;
        left: 65px;
        top: 0px;
        box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.5);

        //min-width: 140px;
        //height: auto;
        //background-color: @menu-bg-color-active;
      }
    }
  }
  .menu.menu-y > li > a {
    .font-size(@font-size-h2);
  }
  .menu-y{
    width: 70px;
    transition: all .5s ease;
    -moz-transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
  }
  #page-wrapper {
    margin: 0 0 0 70px;
  }
  .nav-label,
  .menu.menu-y li a span {
    display: none;
  }
}

@media (min-width: 768px) {
  #page-wrapper {
    position: inherit;
    margin: 0 0 0 220px;
    min-height: 1200px;
  }
  .menu-y {
    z-index: 101;
    position: absolute;
    width: 220px;
  }
}
@media (max-width: 768px) {
  #page-wrapper {
    position: inherit;
    margin: 0 0 0 0px;
    min-height: 1000px;
  }


  #page-wrapper {
    .secondary-menu-btn {
      display: block;
      color: @menu-color-active;
    }
  }

body.mini-navbar #page-wrapper {
    .secondary-menu-btn {
      display: none;
    }
  }

  .body-small .menu-y {
    display: none;
    z-index: 1;
    position: absolute;
    width: 70px;
  }
  
  .body-small.mini-navbar .menu-y {
    display: block;
    transition: all .5s ease;
    -moz-transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
  }
}