.nav-tabs {
  border-bottom: 4px solid @nav-tabs-border-color;
  > li {
    z-index: 102;
    margin-bottom: 0px;

    // Actual tabs (as links)
    > a {
      height: 32px;
      .font-size(@font-size-h4);
      color: @gray-darker;
      margin-right: 6px;
      padding: 5px 10px 5px 10px;
      line-height: @line-height-base;
      border: none;
      //border-radius: 0px;
      //box-shadow: 2px 0px 5px 1px rgba(0, 0, 0, 0.4);
      border-radius: 7px 2px 0px 0px;
      box-shadow: 2px 0px 5px 1px rgba(0, 0, 0, 0.1);
      border : 1px solid @gray-light;
      float: left;

      .glyphicons{
        color: @gray-darker;
        padding-left: 7px;
      }

      background-color: @white;
      &:hover {
        background-color: #e0e0e0;
        border : 1px solid #e0e0e0;
      }
    }

    // Active state, and its :hover to override normal :hover
    &.active{
      > a {
        float: left;
        background-color: @nav-tabs-border-color;
        cursor: pointer;
        .glyphicons{
          color: @white;
        }
        border: none;
        &:hover,
        &:focus{
          cursor: pointer;
          background-color: @nav-tabs-border-color;
          border: none;
          color: @nav-tabs-active-link-hover-color;
        }
      }
    }
  }
}


/* Style du bouton fermer */
ul .close {
  font-size: inherit;
  font-weight: inherit;
  text-shadow: none;
  opacity: 1;
  filter: alpha(opacity=100);
}
