.alert {
  border-top-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 1px;
  background-color: #fff;
  border-radius: 4px;
  font-family: @font-family-serif;
  .font-size(12);

  &.alert-danger {
  	color: @brand-danger;
  }

  &.alert-warning {
  	color: @brand-warning;
  }

  &.alert-info {
  	color: @brand-info;
  }

  &.alert-success {
  	color: @brand-success;
  }
}


