.table {
  > thead{
    > tr {
      th{
        font-weight: bold;
        .font-size(@font-size-base);
        color: #333333;
      }
    }
  }
  > tbody {
    > tr {
      &.active {
        color: @white;
      }
    }
  }
}

.table-hover {

  > tbody {
    > tr{
      &:hover {
        background-color: @table-bg-hover;
        cursor: pointer;
      }
    }
  }
}


/* Tableaux */

table th a.unsorted:after {
  padding-left: 8px;
  padding-right: 3px;
  font-family: "FontAwesome";
  content: "\f0dc";
  text-decoration: none;
  padding-right: 7px;
  color: @brand-primary; /* bleu theme */
  opacity: 0.8;
}

table th a.sortasc:after {
  padding-left: 8px;
  padding-right: 3px;
  font-family: "FontAwesome";
  content: "\f0de";
  text-decoration: none;
  padding-right: 7px;
  color: @brand-primary; /* bleu theme */
  opacity: 0.8;
}

table th a.sortdesc:after {
  padding-left: 8px;
  padding-right: 3px;
  font-family: "FontAwesome";
  content: "\f0dd";
  text-decoration: none;
  padding-right: 7px;
  color: @brand-primary; /* bleu theme */
  opacity: 0.8;
}

table > thead > tr > th {
  background-color: #f3f3f3; /* gris */
  border-color: #f3f3f3;
  text-align: center;
}
table thead tr th {
  border-color: #ddd;
  border-bottom: 1px solid #ddd;
}

table thead th a{
  color:@black; /* noir */
  cursor: pointer;
  text-decoration: none; /*sans surlignage*/
}

table tbody tr {
  text-align: left;
}
table tbody tr:hover {
  background-color: #d3e6f2;
}


/* Pagination */
nav.center {
  text-align: center;
}

ul.pagination li {
  border-radius: 3px;
}
ul.pagination li a{
  color: @brand-primary; /* bleu theme */
  background-color: #f3f3f3; /* gris */
  font-weight: bold;
}

.glyph-inverse {
  -ms-transform: rotate(180deg); /* IE 9 */
    -webkit-transform: rotate(180deg); /* Chrome, Safari, Opera */
    transform: rotate(180deg);
}

