/*
  Style des radios
*/
/* masquer les inputs */
input[type="radio"]:checked,
input[type="radio"]:not(:checked) {
  width: 1px;
  height: 1px;
  margin: 0;
  padding: 0;
  opacity: 0;
  position: absolute;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
}
/* les labels */
input[type="radio"] + label {
  float: left;
  padding-left: 26px;
  padding-left: 2.6rem;
  margin-right: 16px;
  margin-right: 1.6rem;
  position: relative;
  cursor: pointer;
}
/* les fonds des inputs */
input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -9px;
  margin-top: -0.9rem;
  width: 16px;
  width: 1.6rem;
  height: 16px;
  height: 1.6rem;
  background-color: rgba(255, 255, 255, 0.7);
  -webkit-box-shadow: inset 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.3), inset 0 0 0 0.1rem rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.3), inset 0 0 0 0.1rem rgba(0, 0, 0, 0.1);
  -o-box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.3), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  -o-box-shadow: inset 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.3), inset 0 0 0 0.1rem rgba(0, 0, 0, 0.1);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.2), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.3), inset 0 0 0 0.1rem rgba(0, 0, 0, 0.1);
  -webkit-border-radius: .3rem;
  -moz-border-radius: .3rem;
  -o-border-radius: 3px;
  -o-border-radius: .3rem;
  border-radius: 3px;
  border-radius: .3rem;
}
input[type="radio"] + label:before {
  -webkit-border-radius: .8rem;
  -moz-border-radius: .8rem;
  -o-border-radius: 8px;
  -o-border-radius: .8rem;
  border-radius: 8px;
  border-radius: .8rem;
}
/* le focus */
input[type='radio']:focus + label:before {
  border: 1px solid #2a7fb9;
  outline: 0;
  -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(84, 180, 226, 0.6);
  -o-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(84, 180, 226, 0.6);
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(84, 180, 226, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(84, 180, 226, 0.6);
}
/* les radios */
input[type="radio"] + label:after {
  content: '';
  position: absolute;
  left: 2px;
  left: .2rem;
  top: 50%;
  margin-top: -7px;
  margin-top: -0.7rem;
  width: 12px;
  width: 1.2rem;
  height: 12px;
  height: 1.2rem;
  background-color: #406274;
  -webkit-border-radius: .6rem;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  -webkit-box-shadow: inset 0 0.1rem 0.2rem rgba(255, 255, 255, 0.6), inset 0 -0.1rem 0.1rem 0 rgba(0, 0, 0, 0.5), 0 0 0.3rem #000000;
  -moz-box-shadow: inset 0 0.1rem 0.2rem rgba(255, 255, 255, 0.6), inset 0 -0.1rem 0.1rem 0 rgba(0, 0, 0, 0.5), 0 0 0.3rem #000000;
  -o-box-shadow: inset 0 1px 2px rgba(255, 255, 255, 0.6), inset 0 -1px 1px 0 rgba(0, 0, 0, 0.5), 0 0 3px #000000;
  -o-box-shadow: inset 0 0.1rem 0.2rem rgba(255, 255, 255, 0.6), inset 0 -0.1rem 0.1rem 0 rgba(0, 0, 0, 0.5), 0 0 0.3rem #000000;
  box-shadow: inset 0 1px 2px rgba(255, 255, 255, 0.6), inset 0 -1px 1px 0 rgba(0, 0, 0, 0.5), 0 0 3px #000000;
  box-shadow: inset 0 0.1rem 0.2rem rgba(255, 255, 255, 0.6), inset 0 -0.1rem 0.1rem 0 rgba(0, 0, 0, 0.5), 0 0 0.3rem #000000;
  display: none;
}
input[type="radio"]:checked + label:after {
  display: block;
}
