.menu.menu-x {

  font-family: @font-family-serif;
  font-weight: 200;
  //box-shadow: 10px 0px 10px 2px rgba(0, 0, 0, 0.5);
  box-shadow: 7px 0px 10px 0px rgba(0, 0, 0, 0.25);
  border-bottom : 1px solid @gray;

  background-color: @menu-bg-color;
  //position: fixed;
  right: 0;
  left: 0;
  z-index: 1020;

  &.with-menu-y{
    margin-left: 220px;
    transition: all .5s ease;
    -moz-transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
  }

  .menu-toggle{
    display: none;
    z-index: 1;
  }


  ul {
    margin-bottom: 0px;

    li {
      list-style-type: none;
      display: table-cell;
      margin: 0;
      //padding-left: 20px;
      //padding-right: 20px;
      //padding-bottom: 15px;
      transition: all .3s ease;
      -moz-transition: all .3s ease;
      -webkit-transition: all .3s ease;
      -o-transition: all .3s ease;


      &.dropdown {
        :focus {
          .tab-focus()
        }

        a {
          white-space: nowrap;
          & > .glyphicons {
            padding-top: 7px;
            margin-left:4px;
          }        
        }
        
      }

      &.active {
        border-bottom: 4px solid @bl-light;
      }

      &.active a {
        color: #000;
      }
      a{
        white-space: nowrap;
        display: block;
        text-align: left;
        //height: 30px;
        line-height: 24px;
        color: @menu-color-active;
        text-decoration: none;
        font-weight: 200;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 20px;
        padding-right: 20px;
      }

    }
    li:hover {
      background-color: @menu-bg-hover-color;
    }

  }


  // Hover/Focus state
  ul.dropdown-menu {
    padding-top: 0px;
    padding-bottom: 0px;

    > li  {
      padding-left: 0px;
      //padding-bottom: 7px;
      //padding-top: 7px;
      
      display: block;
      background-color: @menu-bg-color;

      transition: all .3s ease;
      -moz-transition: all .3s ease;
      -webkit-transition: all .3s ease;
      -o-transition: all .3s ease;

      &.subactive {
        background-color: @menu-bg-hover-color;
        border-left: 4px solid @bl-light;
      }



      &.head {
        font-weight: normal;  
        color: @text-color;
        
        background-color: @menu-bg-color-active;
        
        padding-top: 5px;
        padding-left: 10px;
        padding-bottom: 5px;
        text-align: left;
      
        &:hover {
            background-color: @menu-bg-color-active;
        }

      }

      & > a {
        //height: 35px;
        //padding-top: 8px;
        &:hover {
          background-color: transparent;
        }
      }

      &:hover {
          background-color: @menu-bg-hover-color;
      }
  
    }
  }   


}



.mini-navbar .menu.menu-x.with-menu-y {
  //position: fixed;
  right: 0;
  left: 0;
  z-index: 1020;
  margin-left: 70px;
  transition: all .5s ease;
  -moz-transition: all .5s ease;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
}

/** Statègie de responsive du menu horizontal */
@media (min-width: 768px) {
  .menu.menu-x {
    .collapse{
      height: auto !important;
      visibility: visible;
      display: block;
    }
  }
}
@media screen and (max-width: 768px) {
  .menu.menu-x {
    margin-left: 0px;
    &.with-menu-y{
      margin-left: 0px;
    }

    .menu-toggle{
      display: block;
      position: absolute;
      right: 3px;
      top: 45px;
    }
    > ul{      
      padding: 0px;
      > li{

        &.active {
          background-color: @menu-bg-hover-color;
          border-bottom: 0px solid @brand-primary;
          border-left: 4px solid @bl-light;
        }
        padding-top: 0px;
        //padding-bottom: 15px;
        width: 100%;
        display: block;
      }
    }

    ul.dropdown-menu {
        width: 100%;
    }


  }

}

