.pace{
  > .pace-progress{
    background-color: lighten(@bl-sante, 20%);
  }
}

.minimalize-styl-2 {  
  /* Pour annuler l'état du bouton du menu visited ou focus */
  &:focus {
    background-color: @bl-sante!important;  
  }
}


.menu.menu-x{
  ul li{
    &.active a:after,
    &:hover a:after{
      border-color: @bl-sante;
    }

    &.active {
      border-bottom: 4px solid @bl-sante;
    }

  }

  // bordure sous-menu x
  ul.dropdown-menu {
    > li  {
      &.subactive {
        border-left: 4px solid @bl-sante;
      }
    }
  }

}

@media screen and (max-width: 768px) {
  .menu.menu-x {
    > ul{      
      > li{
        &.active {
          background-color: @menu-bg-hover-color;
          border-bottom: 0px solid @brand-primary;
          border-left: 4px solid @bl-sante;
        }
      }
    }
  }
}

.menu.menu-y {
  > div > ul,
  > ul {
    > li  {
      &.active{
        border-left: 4px solid @bl-sante;
        &:before{
          color: @bl-sante;
        }
      }
      a{
        .glyphicons,
        .fa{
          color: @bl-sante;
        }
      }
    }
  }
}

// style de l'élément actif du menu
.menu.menu-y{
  > div > ul,
  > ul {
    > li  {

      &.active{
          > a{
            //color: @bl-sante;
          } 
      }
      &.stretchable{
          > ul.nav-second-level {
            > li{
              &.active {
                //background-color: @bl-sante;
                border-left: 4px solid @bl-sante;
                > a {
                  color: @bl-sante;
                  &:hover, 
                  &:focus  {
                    //background-color: @bl-sante;
                  }
                }

              }
            }
          }
      }
    }
  }
}


.nav-tabs {
  border-bottom: 4px solid @bl-sante;
  > li.active > a{
    background-color: @bl-sante;
  }
}
.navbar.navbar-inverse {
  background-color: @bl-sante !important;
}

&.mini-navbar{
  .nav{
    li{
      &.active{
        .nav-second-level{
          //border-left: 4px solid @bl-sante!important;
        }
      }
    }
  }
}

.btn{
  &.btn-primary{
    //.linear-gradient(lighten(@bl-sante, 5%), @bl-sante);
    background-color: @bl-sante;
    border: 1px solid darken(@bl-sante, 10%);
    &:hover,&:focus{
      background: none;
      //background-color: @bl-sante;
      background-color: darken(@bl-sante, 10%);
    }
  }
}

.box .box-header .box-title {
  color: @bl-sante;
}
.box .box-header > .box-tools i, .box .box-header > .box-tools:after {
  color: @bl-sante;
}

table th a.unsorted:after {
  color: @bl-sante!important;
}
table th a.sortasc:after {
  color: @bl-sante!important;
}
table th a.sortdesc:after {
  color: @bl-sante!important;
} 
ul.pagination li a {
  color: @bl-sante!important;
}


.box .glyphicons {
  color: @bl-sante;
}

.btn {
  .glyphicons {
  color: @white;
  }
  &.btn-default {
    .glyphicons {
      color: @bl-sante;
    }
  }
}

.section-action:before {
  color: @bl-sante;
}
.box.section .box-header .box-title:after {
 color: @bl-sante;  
}

.modal-content{
  .modal-header{
    background-color: @bl-sante;
  }
}


.navbar-fixed-top:after{
  width: 100%;
  height: 4px;

  filter:progid:DXImageTransform.Microsoft.gradient(GradientType=1,startColorstr=#F6ED43, endColorstr=#82CCAF);
  background-image:-moz-linear-gradient(left, #F6ED43 0%, #82CCAF 50%,#0B8A7C 100%);
  background-image:linear-gradient(left, #F6ED43 0%, #82CCAF 50%,#0B8A7C 100%);
  background-image:-webkit-linear-gradient(left, #F6ED43 0%, #82CCAF 50%,#0B8A7C 100%);
  background-image:-o-linear-gradient(left, #F6ED43 0%, #82CCAF 50%,#0B8A7C 100%);
  background-image:-ms-linear-gradient(left, #F6ED43 0%, #82CCAF 50%,#0B8A7C 100%);
  background-image:-webkit-gradient(linear, left bottom, right bottom, color-stop(0%,#F6ED43), color-stop(50%,#82CCAF),color-stop(100%,#0B8A7C));
}


[type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    color: @bl-sante;
  }

input[type="radio"]+label:after {
  background-color: @bl-sante;
}