

.badge {
	border: 1px solid @brand-primary; /* bleu theme */
	border-radius: 8px;
	background-color: white;
	color: @brand-primary; /* bleu theme */
}

.badge.badge-up{
  padding:3px 5px;
  margin-top:-10px;
  margin-left: -5px;
  vertical-align: top;
}

.badge-danger {
	background-color: @brand-danger;
	color: white;
}
.badge-info {
	background-color: @brand-info;
	color: white;
}
.badge-success {
	background-color: @brand-success;
	color: white;
}
.badge-warn {
	background-color: @brand-warning;
	color: white;
}
