.breadcrumb {
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 4px;
  padding: 7px 11px;

  > li {
    .font-size(@font-size-h5);
    font-family: @font-family-serif;
    + li:before {
      .font-size(@font-size-h5);
      font-weight: normal;
    }
    a:active{
      color: @breadcrumb-click-color;
    }
  }

  > .active {
    .font-size(@font-size-h2);
    font-weight: 200;
  }
}